.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    max-height: 400px;
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    width: 100%;
    background-color: white ;
    z-index: 1;
  }
  
  .product > img {
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
  }
  
  .product > button {
    background-color: #f0c14b;
    border: 1px solid;
    width: 150px;
    cursor: pointer;
    border-color: #a88734 #9c7e31 #846a29;
  }
  
  .price {
    margin-top: 5px;
  }
  
  .info {
    height: 100px;
    margin-bottom: 15px;
  }
  
  .rating {
    display: flex;
  }
  
  .title {
    color: black;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  
  i {
    vertical-align: middle;
  }
.home {
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    display: flex;
  }
  
  .item-container {
    display: flex;
    color: white;
    background-color: #2b2727
  }
  
  .item-container p {
    margin: 10px;
    font-size: 14px;
    font-family: Arial, sans-serif;
  }
  
  .home-row {
    display: flex;
    z-index: 1;
    margin-top:20px ;
    margin-left: 5px;
    margin-right: 5px;
  }
  .custom{
    margin-top: 80px;
  }
  
  .home-image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  }
section {
    width: 80vw;
    margin: 5rem auto;
    max-width: 1000px;
    /* width: 100%; */
  }  
  
  @media screen and (min-width: 992px) {
    .section {
      width: 95vw;
    }
  }
  
  
  .section-center {
    height: 250px;
    text-align: center;
    display: flex;
    overflow: hidden;
  }
  
  article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    opacity: 0;
    transition: all 0.3s linear;
  }
  
  article.activeSlide {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  article.lastSlide {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  article.nextSlide {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  
  .banner-img {
    margin-top: 97px;
    margin-left: 10px;
    margin-right: -10px;
    width: 100%;
    z-index: -1;
    margin-bottom: -140px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  }
  
  .prev,
  .next {
    position: absolute;
    top: 200px;
    margin-left: 10px;
    color: black;
    width: 1.25rem;
    height: 1.25rem;
    display: grid;
    place-items: center;
    cursor: pointer;
  }
  
  .prev {
    left: 0;
  }
  .next {
    right: 0;
  }
  @media (min-width: 800px) {
    .text {
      max-width: 45em;
    }
    .prev,
    .next {
      width: 2rem;
      height: 2rem;
      font-size: 2.5rem;
    }
  }
.scroll-to-top {
    /* position: fixed; */
    /* bottom: 0.5rem; */
    /* right: 0.5rem; */
    -webkit-animation: fadeIn 700ms ease-in-out 1s both;
            animation: fadeIn 700ms ease-in-out 1s both;
    cursor: pointer;
  }
  
  @-webkit-keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  .back-top-container {
    display: flex;
    align-items: center;
    height: 40px;
    text-align: center;
    justify-content: center;
    width: 100%;
    background-color: #37475a;
    color: white;
    margin-right: 15px;
  }

